.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
  text-align: center;
}

a > * {
  cursor: pointer;
  padding: 1rem;
  min-width: 30px;
  min-height: 30px;
  background: rgb(238, 232, 232);
  border-radius: 50%;
  border: 3px solid black;
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.X {
  display: flex;
  justify-content: flex-end;
  font-size: 2.25rem;
  cursor: pointer;
}

.center {
  text-align: center;
}

.modal-button {
  display: flex;
  justify-content: center;
}

.player-title {
  padding: 0.5rem;
  font-size: 1.5rem;
}

.submit {
  margin: 1rem;
  padding: 1rem;
  font-size: 1.25rem;
  background-color: orange;
  border: none;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.modal-inputs {
  padding: 0.5rem;
  text-align: center;
  margin-right: 4rem;
  margin-left: 4rem;
}

.ms {
  font-size: 1.5rem;
}

.modal-inputs > input {
  margin: 0.5rem;
  padding: 0.5rem;
  width: 2.5rem;
  background-color: rgb(124, 124, 124);
  font-size: 2rem;
  text-align: center;
  border: 1px solid black;
  color: white;
}

.bob {
  transform: scale(-1, -1);
}

.move {
  font-size: 3rem;
}

a {
  display: flex;
  margin: 0.5rem;
  padding: 1rem;
}

.play {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: transparent;
  color: black;
  font-size: 2rem;
}

.black {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(41, 41, 41);
  font-family: sans-serif;
  font-size: 7rem;
  padding: 1rem;
  color: white;
  padding: 0px;
  margin: 0px;
  flex-grow: 1;
}
.white {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(206, 206, 206);
  font-family: sans-serif;
  font-size: 7rem;
  padding: 1rem;
  color: black;
  padding: 0px;
  margin: 0px;
  flex-grow: 1;
}

p {
  margin: 0px;
  cursor: default;
  user-select: none;
}

html,
body {
  padding: 0px;
  font-family: sans-serif;

  margin: 0px;
}

